	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/link-icon-generator-pro/index.md absPath of file >>> MarkdownRemark","html":"<p>[\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/icon-generator-pro-88f23f986dd37bbb45d3967adb2d81fa-41a8d.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"icon generator pro\"\n        title=\"\"\n        src=\"/static/icon-generator-pro-88f23f986dd37bbb45d3967adb2d81fa-41a8d.png\"\n        srcset=\"/static/icon-generator-pro-88f23f986dd37bbb45d3967adb2d81fa-bf474.png 158w,\n/static/icon-generator-pro-88f23f986dd37bbb45d3967adb2d81fa-d5dd3.png 315w,\n/static/icon-generator-pro-88f23f986dd37bbb45d3967adb2d81fa-41a8d.png 500w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>I love this application, Icon Generator Pro.  It's simple, but sometimes that's a good thing in an application, means it has focus - if you're going to do something, do it properly - that's one of the benefits of AIR, it makes it simple to create applications just like this.</p>\n<p>What the application actually does is just generate images in a few specific formats, output the files (in a format useful for your own AIR applications) and even, from the looks of it, replace your Twitter icon with your new file.</p>\n<p>If you haven't seen the earlier version of this application, its still available from the AIR Marketplace <a href=\"http://www.adobe.com/cfusion/marketplace/index.cfm?event=marketplace.offering&#x26;offeringid=10313&#x26;marketplaceid=1\">[link]</a>, and the newer version, Icon Generator Pro is available from <a href=\"http://icon-generator.net/\">http://icon-generator.net/</a>.</p>","frontmatter":{"title":"Link: Icon Generator Pro","date":"May 12, 2010","tags":["air-marketplace","icons"],"url":null,"image":null,"description":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/link-hiding-the-complexity-of-your-tasks-with-bespoke-solutions/","tags":["external-links","mmt-digital"],"title":"Layers of simplicity: Hiding the complexity of your tasks with bespoke solutions.","image":null,"date":"2010-04-29T11:42:06.000Z"}},"next":{"frontmatter":{"path":"/blog/i-am-now-an-iphone-app-developer/","tags":["iphone"],"title":"I am now an iPhone app developer.","image":null,"date":"2010-05-25T21:47:28.000Z"}}}}

